@import '~antd/dist/antd.dark.css';
.App {
  align-items:center;
}
.App h1, h2, h3, h4, h5, h6 {
  color: #fff;
  font-weight: bold;
}
ul{
  list-style-type:none;
  padding:0;
}


.App-title img{
  transition: all 0.4s;
  margin: 0;
  height: 6rem;
  line-height: 1.2;
}

.App main,header,footer {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items:  center;
}
.App header,footer {
  padding: 2rem 1rem;
}
.App header{
  z-index: 2;
  position: fixed;
  top: 0;
  background-color: #0c0c0c;
  border-bottom: 1px solid rgba(255, 255, 255, 0.16);
}

.App header .header-layout{
  display: flex;
  justify-content: space-between;
}
.App header *{
  transition: all 0.4s;
}


.App header.min{
  padding: 0.8rem 1rem;
}

.App header.min .App-title img{
  transition:all 0.4s;
  margin: 0;
  height: 2rem;
}

.header-layout, .App-chart,.App-article{
  width: 860px;
  max-width: 100%;
}


.App main{
  margin: 12rem 0 3rem 0;
}
.App-chart-title{
  font-weight: bold;
  margin: 0 0 1rem 0;
}

.App-chart{
  margin: 1.5rem 0;
}

.App-article {
  padding: 1.5rem;
}
.App-chart-instructions,.App-R0-instructions {
  padding: 1rem;
  background-color: #161616;
  overflow: hidden;
  height: 100%;
}
.App-chart-instructions li{
  margin-top: 1.5rem;
}

.App-R0-instructions img{
  float: left;
}
.App-R0-instructions-article {
  float: left;
  margin-top: -368px;
  padding: 1.6rem;
  background-color: rgba(0,0,0,0.72);
}
.App-R0-instructions-article strong{
  color: #fff;
}



